import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { WorkTemplate } from './work-template/work-template';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class WorkService {
  private experiencesUrl = '/assets/experiences.json';
  constructor(private httpClient: HttpClient) { }

  getExperiences(): Observable<WorkTemplate[]> {
    return this.httpClient.get<WorkTemplate[]>(this.experiencesUrl);
  }
}
