import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { SpeakTemplate } from './speak-template/speak-template';

@Injectable({
  providedIn: 'root'
})
export class SpeakingService {
  private speakingExperiencesUrl = '/assets/speaking-experiences.json';
  constructor(private httpClient: HttpClient) { }

  getSpeakingExperiences(): Observable<SpeakTemplate[]> {
    return this.httpClient.get<SpeakTemplate[]>(this.speakingExperiencesUrl);
  }
}
