import { Component, OnInit } from '@angular/core';
import { SpeakingService } from './speaking.service';
import { SpeakTemplate } from './speak-template/speak-template';

@Component({
  selector: 'app-speaking',
  templateUrl: './speaking.component.html',
  styleUrls: ['./speaking.component.css']
})
export class SpeakingComponent implements OnInit {
  speakingExperiences: SpeakTemplate[];
  constructor(private speakService: SpeakingService) { }

  ngOnInit() {
    this.speakService.getSpeakingExperiences()
      .subscribe(
        experiences => this.speakingExperiences = experiences,
        error => console.log(error)
      );
  }

}
